.main--nav{
    display:flex;
    position: fixed;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height:60px;
    z-index: 10;
}

.nav--logo{
    display: inline-block;
    padding-left: 20px;
    font-size: 40px;
    font-family: 'Sacramento', cursive;
    color: black;
    text-decoration: none;
}
.nav--link--container{
    display:none;
    padding-right:20px;
}
.nav--link{
    font-size: 30px;
    color:black;
    font-family:  'Sacramento', cursive;
    text-decoration: none;
}

.ham--menu{
    position: absolute;
display: inline-block;
height:40px;
background-repeat: no-repeat;
margin-right: 10px;
aspect-ratio: 1/1;
background-image: url('../img/ham-menu.png');
background-size: contain;
cursor: pointer;
transition: transform 0.5s ease-in-out;
right:0;
transform: translateX(0) rotate(0);
top:10px;
z-index: 10;
}

.ham--exit{
    position: absolute;
    display: inline-block;
    height:40px;
    background-repeat: no-repeat;
    margin-right: 10px;
    aspect-ratio: 1/1;
    background-image: url('../img/x.png');
    background-size: contain;
    cursor: pointer;
    transform: translateX(60px) rotate(90deg);
    transition: transform 0.5s ease-in-out;
    right:0;
    top:10px;
    z-index: 10;
}

/* DROPDOWN MENU */
.side--nav{
    display: inline-block;
    height:100vh;
    position: fixed;
    right:0;
    top:0;
    width: 70px;
    background-color: rgba(234, 234, 234, 0.708);
    z-index: 5;
    transform: translateX(80px);
    transition:transform 0.5s ease-in-out;
}

.side--nav--container{
    z-index: 4;
    display: inline-block;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top:-100vh;
    left:0;
    background-color: rgba(0, 0, 0, 0.616);
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}