*{
    margin:0;
    padding:0;
}

body{
    height: 200vh;
   position: relative;
   overflow-x: hidden;
}

.hero{
    width:100%;
    height:100vh;
    position: absolute;
    left:0;
    top:0;
    background-size: cover;
    background-image: url('../img/hero.jpg');
    z-index: -1;
}

.page--one{
    height:100vh;
    width:100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
}

.page--one--title{
    font-family:  'Sacramento', cursive;
    font-size:13vw;
    color:#000;
    text-shadow: 4px 2px 5px #fff;
    font-weight: 200;
    width:100%;
    text-align: center;
    z-index: 2;
}

.page--one--desc{
    font-family:  'Sacramento', cursive;
    font-size:5vw;
    color:rgb(255, 255, 255);
    text-shadow: 2px 2px 5px rgb(0, 0, 0);
    font-weight: 200;
    width:100%;
    text-align: center;
    z-index: 2;
}